
import XButton from 'vux/src/components/x-button/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default {
name: 'Home',
data () {
return {
imgList: [],
tickerList: ['涨幅榜', '新币榜'],
nowTicker: '涨幅榜',
tab_index: 0
}
},
created () {
this.getBanner()
},
methods: {
getBanner () {
this.imgList = [
{
'key_word': '6',
'type': 1,
'img': {
'url': 'https://huobi-1253283450.file.myqcloud.com/bit/banner/670040a2-ace6-4d95-81f6-a600e803e6db.png'
}
}
]
},
onItemClick (index) {
if (this.tab_index === index) return
console.log(index)
this.tab_index = index
}
},

components: {
XButton,
Marquee,
MarqueeItem,
Group,
Swiper,
SwiperItem,
Card,
Tab,
TabItem
}
}



